@charset "UTF-8";

/* normalize */
body,
ul,
ol,
li,
p,
strong,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

button {
  margin: 0;
  padding: 0;
  font: inherit;
}

input[type=submit] {
  font-family: inherit;
}

* {
  box-sizing: border-box;
  font-size: 62.5%;
}

button {
  margin: 0;
  padding: 0;
  font: inherit;
  border: none;
  outline: none;
  background: none;
  cursor: pointer;
}

input[type=submit] {
  font-family: inherit;
}

hr {
  margin: 0;
  padding: 0;
}

p,
a,
li,
ol,
div,
span {
  font-size: 1.6rem;
}

/* common */
.con {
  margin-left: auto;
  margin-right: auto;
}

.cursor_p {
  cursor: pointer;
}

.img_box {
  box-sizing: border-box;
  width: 100%;
  text-align: center;
}

.img_box>img {
  max-width: 100%;
}

.bg_img_box {
  box-sizing: border-box;
  width: 100%;
  text-align: center;
}

/* display */
.block {
  display: block;
}

.inline {
  display: inline;
}

.inline_block {
  display: inline-block;
}

.inline_flex {
  display: inline-flex;
}

.none {
  display: none;
}

.none_im {
  display: none !important;
}

/* position */
.fixed {
  position: fixed;
}

.relative {
  position: relative;
}

.absolute {
  position: absolute;
}

/* absolute */
.absolute_top {
  position: absolute;
  top: 0;
}

.absolute_left {
  position: absolute;
  left: 0;
}

.absolute_right {
  position: absolute;
  right: 0;
}

.absolute_bottom {
  position: absolute;
  bottom: 0;
}

.absolute_top_center {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.absolute_bottom_center {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.absolute_center_center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}

/* float */
.row::after {
  content: "";
  clear: both;
  display: block;
}

.cell {
  float: left;
  box-sizing: border-box;
}

.cell_right {
  float: right;
  box-sizing: border-box;
}

/* flex */
.flex {
  display: flex;
  -ms-flex: flex;
}

.flex_imp {
  display: flex !important;
  -ms-flex: flex;
}

.flex_auto {
  display: flex;
  flex: auto;
}

.flex_1_0_0 {
  display: flex;
  flex: 1 0 0;
  -ms-flex: flex;
  -ms-flex: 1 0 auto;
}

.flex_wrap_no {
  flex-wrap: nowrap;
  -ms-flex-wrap: nowrap;
}

.flex_wrap_wrap {
  flex-wrap: wrap;
  -ms-flex-wrap: wrap;
}

.flex_jc_sa {
  display: flex;
  justify-content: space-around;
}

.flex_jc_sb {
  display: flex;
  justify-content: space-between;
  -ms-flex-pack: justify;
}

.flex_jc_se {
  display: flex;
  justify-content: space-evenly;
}

.flex_jc_c {
  display: flex;
  justify-content: center;
  -ms-flex-pack: center;
}

.flex_jc_s {
  display: flex;
  justify-content: flex-start;
  -ms-flex-pack: start;
}

.flex_jc_e {
  display: flex;
  justify-content: flex-end;
  -ms-flex-pack: end;
}

.flex_ai_c {
  display: flex;
  align-items: center;
  -ms-flex-align: center;
}

.flex_ai_fs {
  display: flex;
  align-items: flex-start;
  -ms-flex-align: start;
}

.flex_ai_fe {
  display: flex;
  align-items: flex-end;
  -ms-flex-align: end;
}

.flex_ai_str {
  display: flex;
  align-items: stretch;
  -ms-align-items: stretch;
}

.flex_as_s {
  display: flex;
  align-self: flex-start;
}

.flex_as_c {
  display: flex;
  align-self: center;
}

.flex_as_e {
  display: flex;
  align-self: flex-end;
}

.flex_dir_c {
  display: flex;
  flex-direction: column;
}

.flex_dir_c_r {
  display: flex;
  flex-direction: column-reverse;
}

.flex_dir_r {
  display: flex;
  flex-direction: row;
}

.flex_dir_r_r {
  display: flex;
  flex-direction: row-reverse;
}

.flex_flow_w {
  flex-flow: wrap;
}

.flex_flow_c {
  flex-flow: column;
}

/* flex-basis */
.flex_basis_100 {
  flex-basis: 100%;
  max-width: 100%;
}

